import { LitElement, html, css} from 'lit-element';

class SettingPanel extends LitElement {
  static get styles() {
    return [
      GlobalStyles,
      css`
        :host {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: var(--setting-panel-height);
          background-color: #343334;
          color: #fff;
        }
        form {
          width: 100%;
        }
        fieldset {
          border: 0;
        }
        input[type="number"] {
          width: 5em;
        }
        #url {
          width: 50%;
        }
      `
    ];
  } // style()

  static get properties() {
    return {
      _url: { type: String },
      _currentViewportW: { type: Number },
      _currentViewportH: { type: Number }
    }
  } // properties()

  constructor() {
    super();

    this._url = Global.setting.url;
    this._currentViewportW = Global.setting.viewportW;
    this._currentViewportH = Global.setting.viewportH;
  } // constructor()

  // firstUpdated() {
  // }

  updateCurrentViewport(w, h) {
    this._currentViewportW = w;
    this._currentViewportH = h;
    let state = StateManager.getState(this);
    let newSetting = Object.assign({}, state.setting, { viewportW: w, viewportH: h, updatedAt: new Date().getTime() });
    StateManager.setState(this, { setting: newSetting });
  }

  render() {
    return html`
      <!-- URL -->
      <fieldset>
        <label>URL</label>
        <input type="text" .value=${this._url} @change=${e => {
          this._url = e.currentTarget.value;
          let state = StateManager.getState(this);
          let newSetting = Object.assign({}, state.setting, { url: this._url, updatedAt: new Date().getTime() });
          StateManager.setState(this, { setting: newSetting });
        }} />
      </fieldset>

      <!-- device size and its viewport -->
      <fieldset>
        <select @change=${e => {
          let {s, w, h} = JSON.parse(e.currentTarget.value);
          this.updateCurrentViewport(w, h);
        }}>
          ${Global.setting.devices.map((device, index) => {
            let value = `{"s":"${device.size}","w":${device.viewportW},"h":${device.viewportH}}`;
            return html`<option value=${value} ?selected=${index===0}>${device.size}</option>`}
          )}
        </select>

        <!-- viewport w x h -->
        <input type="number" .value=${this._currentViewportW} @change=${e => {
          let w = Number(e.currentTarget.value);
          this.updateCurrentViewport(w, this._currentViewportH);
        }}></input>
        x
        <input type="number" .value=${this._currentViewportH} @change=${e => {
          let h = Number(e.currentTarget.value);
          this.updateCurrentViewport(this._currentViewportW, h);
        }}></input>

        <!-- reset to current size default -->
        <button @click=${e => {
          let {s, w, h} = JSON.parse(this.shadowRoot.querySelector('select').value);
          this.updateCurrentViewport(w, h);
        }}>Reset</button>
      </fieldset>
    `;
  }
}

customElements.define('setting-panel', SettingPanel);
