/*
  Module to control state of state component and its child components
  The object state is to be set to the state component
*/

// find closest of shadow host (light DOM)
function closestElement(selector, base) {
  if (!base) return null;

  let __closestFrom = el => {
    if (!el || el === document || el === window)
        return null;
    if (el.assignedSlot)
        el = el.assignedSlot;
    let found = el.closest(selector);
    return found
        ? found
        : __closestFrom(el.getRootNode().host);
  }
  return __closestFrom(base);
}

// find all descendants of shadow host (light DOM)
function findElement(base, selector = '') {
  if (!base) return [];

  let foundList = [], item;
  let __findFrom = (el, selector) => {
    let children = el.shadowRoot ? el.shadowRoot.children : el.children;
    for (let i=0; i < children.length; i++) {
      item = children[i];
      if (item.shadowRoot && (!selector || selector.indexOf(item.localName) > -1)) foundList.push(item)
      __findFrom(item, selector)
    }
  }
  __findFrom(base, selector);

  return foundList;
}

const StateManager = {
  /*
    set this attribute to the component if it is state component (i.e. Questionv1.js)
  */
  stateComponentSelector: '[state-component]',

  /*
    currentComponent {element}: current component (descendant)
    stateComponentTag {string}: state component of current component (ancestor)
  */
  getState(currentComponent) {
    console.assert(currentComponent);
    let stateComponent = closestElement(this.stateComponentSelector, currentComponent) || {};
    return Object.assign({}, stateComponent.state || {});
  },

  /*
    currentComponent {element}: current component to fire event "stateChanged"
    stateComponentTag {string}: state component
    state {object}: state to be passed to all descendant components
  */
  setState(currentComponent, state) {
    console.assert(currentComponent && state);
    let stateChangedEvent = new CustomEvent('stateChanged', {
      bubble: false,  // disable event propagation to ancentors
      detail: state   // use event.detail to carry custom data
    });

    // trigger event for itself and its descendants
    let stateComponent = currentComponent.hasAttribute(this.stateComponentSelector)
      ? currentComponent
      : closestElement(this.stateComponentSelector, currentComponent);
    if (stateComponent) {
      stateComponent.state = Object.assign({}, stateComponent.state, state);
      stateComponent.dispatchEvent(stateChangedEvent);
      findElement(stateComponent).forEach(el => el.dispatchEvent(stateChangedEvent));
    } else {
      console.warn('StateManager.setState(): No state component found for current component');
      console.log(currentComponent);
    }
  }
}

// export to global
window.StateManager = StateManager;