import { LitElement, html, css, unsafeCSS } from 'lit-element';
import rotateIcon from '../assets/img/rotate.png';

class DeviceFrame extends LitElement {
  static get styles() {
    return [
      GlobalStyles,
      css`
        :host {
          width: calc(var(--device-w) + 20px);
          height: calc(var(--device-h) + 40px);
          perspective: 1000px;
          user-select: none;
          --opacity: 1;
        }
        iframe {
          border: 0;
          width: 100%;
          height: 100%;
          transform: scale(1);
          background-color: #fff;
        }
        .frame[point-down] iframe {
          pointer-events: none;
        }
        .drag-handle {
          display: none;
          width: 200%;
          height: 200%;
          position: absolute;
          top: -50%;
          left: -50%;
        }
        .frame[point-down] .drag-handle {
          display: block;
        }
        .rotate-handle {
          position: absolute;
          top: 0;
          padding: 15px;
          cursor: url(${unsafeCSS(rotateIcon)}) 0 20, auto;
        }
        .rotate-handle.right {
          right: 0;
        }
        .rotate-handle.left {
          left: 0;
        }
        .resize-handle {
          position: absolute;
          bottom: 0;
          padding: 15px;
        }
        .frame[point-down] .resize-handle {
          padding: 80px;
          transform: translate(80px, 80px);
        }
        .resize-handle.left {
          left: 0;
          cursor: nesw-resize;
        }
        .resize-handle.right {
          right: 0;
          cursor: nwse-resize;
        }
        .frame {
          width: 100%;
          height: 100%;
          will-change: transform;
          transition: transform .5s;
          transform-style: preserve-3d;
          position: relative;
          cursor: move;
          opacity: var(--opacity);
        }
        .frame.flipped {
          --opacity: 1;
          transform: rotateY(180deg);
        }
        .face {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          color: #fff;
          box-sizing: border-box;
          background-color: var(--device-color);
          backface-visibility: hidden;
          box-shadow: rgba(255, 255, 255, 0.5) 0px 0px 3px 1px;
          border-radius: 8px;
        }
        :host([theme="light"]) .face {
          color: var(--dark-color);
        }
        .face.front {
          padding: 18px 10px 30px;
        }
        .face.back {
          padding: 30px;
          transform: rotateY(180deg);
        }
        .frame.flipped .face.front {
          z-index: -1;
        }
        input {
          width: 100%;
        }
        input[type="number"] {
          width: 5em;
        }
        .viewport-size {
          position: absolute;
          top: 0;
          line-height: 18px;
          font-size: .7em;
        }
        .main-btn {
          position: absolute;
          bottom: 0;
          left: 0;
          margin: 7px auto;
          right: 0;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          box-shadow: 0px 0px 2px 1px rgba(255,255,255, .5);
          box-sizing: border-box;
          cursor: pointer;
        }
        :host([theme="light"]) .main-btn {
          box-shadow: 0px 0px 2px 1px var(--dark-color);
        }
      `
    ];
  } // style()

  static get properties() {
    return {
      _url: { type: String },
      _viewportH: { type: Number },
      _viewportW: { type: Number },
      _setting: { type: Object },
      _reloadAt: { type: Number },
      _operation: { type: String }
    }
  } // properties()

  constructor() {
    super();

    this._operation = null;
    this._url = 'about:blank';
    this._viewportW = 0;
    this._viewportH = 0;
    this._reloadAt = 0;
    this._setting = {};

    // static properties
    this._url = Global.setting.url;
    this._viewportW = Global.setting.viewportW;
    this._viewportH = Global.setting.viewportH;
    this._id = null;
    this._flipped = false;
  } // constructor()

  firstUpdated() {
    const move = (dir, step) => {
      let currentLeft = Number(getComputedStyle(this).getPropertyValue('--xpos').trim().replace('px',''));
      let currentTop = Number(getComputedStyle(this).getPropertyValue('--ypos').trim().replace('px',''));
      switch (dir) {
        case 'left':
          this.style.setProperty('--xpos', (currentLeft-=step)+'px');
          break;
        case 'right':
          this.style.setProperty('--xpos', (currentLeft+=step)+'px');
          break;
        case 'up':
          this.style.setProperty('--ypos', (currentTop-=step)+'px');
          break;
        case 'down':
          this.style.setProperty('--ypos', (currentTop+=step)+'px');
          break;
        default:
          // not move
      }
      this.updateState();
    }

    // mouse move on this device handling
    // such moving, resizing
    this.addEventListener('mousemove', e => {
      if (this._operation === 'moving') {
        e.preventDefault();
        let currentX = e.clientX - this.initialX;
        let currentY = e.clientY - this.initialY;
        this.style.setProperty('--xpos', currentX+'px');
        this.style.setProperty('--ypos', currentY+'px');
        this.updateState();
      }

      if (this._operation === 'resizing') {
        e.preventDefault();
        let w = this._viewportW + (e.clientX - this.currentX);
        let h = this._viewportH + (e.clientY - this.currentY);
        this.style.setProperty('--device-w', w+'px');
        this.style.setProperty('--device-h', h+'px');
      }
    });

    // mouse down on this device
    // such start drag, start resize
    this.addEventListener('mousedown', e => {
      if (this._operation != 'resizing') {
        let xoffset = Number(getComputedStyle(this).getPropertyValue('--xpos').trim().replace('px', ''));
        let yoffset = Number(getComputedStyle(this).getPropertyValue('--ypos').trim().replace('px', ''));
        this.initialX = e.clientX - xoffset;
        this.initialY = e.clientY - yoffset;
        this._operation = 'moving';
      }
    });

    // mouse up on this device
    // stop current operation
    this.addEventListener('mouseup', e => {
      this._operation = null;
      this.updateState();
    });

    // keydown on this device
    // movement by key
    this.addEventListener('keydown', e => {
      if (this._flipped) return;

      // "+" : opacity increase
      if (e.altKey && e.keyCode === 107) {
        let opacity = Number(getComputedStyle(this).getPropertyValue('--opacity'));
        this.style.setProperty('--opacity', Math.min(1, opacity+=0.1));
      } else
      // "+" : opacity decrease
      if (e.altKey && e.keyCode === 109) {
        let opacity = Number(getComputedStyle(this).getPropertyValue('--opacity'));
        this.style.setProperty('--opacity', Math.max(0, opacity-=0.1));
      } else
      // "1-4" : device size
      if (!e.ctrlKey && !e.altKey && e.keyCode >= 49 && e.keyCode <= 52) {
        let size = {
          49: 'small',
          50: 'small2',
          51: 'small3',
          52: 'medium'
        }
        let key = e.keyCode;
        let ev = {currentTarget: this.shadowRoot.querySelector('select')};
        this.shadowRoot.querySelector(`option[size="${size[key]}"]`).selected = true;
        this.onChangeViewportSize(ev);
      } else
      // movement
      if (!e.altKey && e.keyCode >= 37 && e.keyCode <= 40) {
        let direction = { 37: 'left', 38: 'up', 39: 'right', 40: 'down' }
        let step = e.ctrlKey ? 10 : 1;
        move(direction[e.keyCode], step);
        e.stopPropagation();
        e.preventDefault();
      }
    });

    // assign id
    this._id = this.getAttribute('id');
  }

  updateState() {
    let newState = StateManager.getState(this);
    let x = Number(getComputedStyle(this).getPropertyValue('--xpos').trim().replace('px',''));
    let y = Number(getComputedStyle(this).getPropertyValue('--ypos').trim().replace('px',''));
    let w = Number(getComputedStyle(this).getPropertyValue('--device-w').trim().replace('px','')) + 20;
    let h = Number(getComputedStyle(this).getPropertyValue('--device-h').trim().replace('px','')) + 40;
    console.log(newState.stuff[this._id]);
    Object.assign(newState.stuff[this._id], { xpos: x, ypos: y, width: w, height: h });
    StateManager.setState(this, { stuff: newState.stuff });
  }

  resize(e) {
    e.preventDefault();
    this._operation = 'resizing';
    this.currentX = e.clientX;
    this.currentY = e.clientY;
  }

  release(e) {
    e.preventDefault();
    this._operation = null;
    this._viewportH = Number(getComputedStyle(this).getPropertyValue('--device-h').trim().replace('px',''));
    this._viewportW = Number(getComputedStyle(this).getPropertyValue('--device-w').trim().replace('px',''));
    this.updateState();
  }

  rotate(e) {
    e.preventDefault();
    let h = this._viewportW;
    let w = this._viewportH;
    this._viewportW = w;
    this._viewportH = h;
    this.style.setProperty('--device-w', w+'px');
    this.style.setProperty('--device-h', h+'px');
    this.updateState();
  }

  onChangeViewportSize(e) {
    let { viewportW, viewportH } = JSON.parse(e.currentTarget.value);
    this._viewportW = viewportW;
    this._viewportH = viewportH;
    this.style.setProperty('--device-w', this._viewportW+'px');
    this.style.setProperty('--device-h', this._viewportH+'px');
    this.updateState();
  }

  render() {
    // if setting is updated by SettingPanel.js, apply to this device
    if (this._reloadAt && this._setting.updatedAt === this._reloadAt) {
      this._viewportW = this._setting.viewportW;
      this._viewportH = this._setting.viewportH;
      this._url = this._setting.url;
      this._reloadAt = 0;
    }

    this.style.setProperty('--device-w', this._viewportW+'px');
    this.style.setProperty('--device-h', this._viewportH+'px');

    return html`
      <!-- device frame -->
      <div class="frame" tabindex="0"
        ?point-down=${this._operation}
        @click=${e => {
          e.preventDefault();
          if (e.ctrlKey) {
            e.currentTarget.classList.toggle('flipped');
            this._flipped = !this._flipped;
          }
        }}}>
        <div class="drag-handle"></div>
        <!-- viewport content -->
        <div class="face front">
          <span class="viewport-size">${this._viewportW} x ${this._viewportH}</span>
          <iframe src=${this._url}></iframe>
          <span class="main-btn"></span>
        </div>
        <!-- device setting -->
        <div class="face back">
          <!-- url -->
          <fieldset>
            <label>URL</label>
            <input type="text" .value=${this._url}
              @change=${e => {
                e.preventDefault();
                this._url = e.currentTarget.value;
              }}
            />
          </fieldset>

          <!-- device size  -->
          <fieldset>
            <select @change=${this.onChangeViewportSize}>
              ${Global.setting.devices.map((device, index) => {
                // let value = `{"s":"${device.size}","w":${device.viewportW},"h":${device.viewportH}}`;
                let value = JSON.stringify(device);
                return html`<option size=${device.size} value=${value} ?selected=${index===0}>${device.size}</option>`}
              )}
            </select>
          </fieldset>

          <!-- viewport size -->
          <fieldset>
            <input type="number" .value=${this._viewportW} @change=${e => {
              this._viewportW = Number(e.currentTarget.value);
              this.style.setProperty('--device-w', this._viewportW+'px');
            }} />
            x
            <input type="number" .value=${this._viewportH} @change=${e => {
              this._viewportH = Number(e.currentTarget.value);
              this.style.setProperty('--device-h', this._viewportH+'px');
            }} />
          </fieldset>
        </div>

        <!-- rotate handler -->
        <a href="" class="rotate-handle right" @click=${this.rotate}></a>

        <!-- resize handler -->
        <span class="resize-handle right"
          @mouseup=${this.release}
          @mousedown=${this.resize}
          @click=${this.release}></span>
      </div>
    `;
  }
}

customElements.define('device-frame', DeviceFrame);
