import { LitElement, html, css, unsafeHtml} from 'lit-element';

class RulerLayer extends LitElement {
  static get styles() {
    return [
      GlobalStyles,
      css`
        :host {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          --tape-pos: 80px;
          /*pointer-events: none;*/
        }
        svg {
          width: 100%;
          height: 100%;
          shape-rendering: crispEdges;
        }
        .tapes {
          position: absolute;
          background-color: red;
          color: #fff;
          line-height: 1.2;
          opacity: .7;
        }
        .x-distance {
          top: var(--tape-pos);
        }
        .y-distance {
          left: var(--tape-pos);
        }
      `
    ];
  } // style()

  static get properties() {
    return {
      _hidden: { type: Boolean, reflect: true },
      _xpos: { type: Number },
      _ypos: { type: Number },
      _keyCode: { type: Number }
    }
  } // properties()

  constructor() {
    super();

    this._hidden = true;
    this._xpos = 450;
    this._ypos = 300;
    this._keyCode = 0;

    // static properties
    this.lines = [];
  } // constructor()

  firstUpdated() {
    this.addEventListener('stateChanged', e => {
      let state = StateManager.getState(this);
      this._hidden = state.activeLayer != 'ruler-layer';
      // gain control after re-render
      if (!this._hidden) {
        setTimeout(function() { this.focus() }.bind(this), 300)
      }
    });

    this.addEventListener('keydown', e => {
      // if (!e.altKey && !e.ctrlKey && e.keyCode === 16) {
      //   this._hidden = !this._hidden;
      // } else
      // create horizontal line by 'h'
      if (!e.altKey && !e.ctrlKey && e.keyCode === 72) {
        let newLine = this.xLine.cloneNode();
        let newTapes = this.xTapes.cloneNode(true);
        this.lines.push({ line: newLine, tapes: newTapes });
      } else
      // create horizontal line by 'v'
      if (!e.altKey && !e.ctrlKey && e.keyCode === 86) {
        let newLine = this.yLine.cloneNode();
        let newTapes = this.yTapes.cloneNode(true);
        this.lines.push({ line: newLine, tapes: newTapes });
        this.requestUpdate();
      } else
      // remove last line by 'z'
      if (!e.altKey && !e.ctrlKey && e.keyCode === 90 && this.lines.length) {
        this.lines.pop();
        this.requestUpdate();
      } else
      // remove all lines by 'x'
      if (!e.altKey && !e.ctrlKey && e.keyCode === 88 && this.lines.length) {
        this.lines = [];
        this.requestUpdate();
      } else
      if (!e.altKey && e.keyCode >= 37 && e.keyCode <= 40) {
        let dir = { 37: 'left', 38: 'up', 39: 'right', 40: 'down' }
        let step = e.ctrlKey ? 10 : 1;
        switch (dir[e.keyCode]) {
          case 'left':
            this._xpos-=step;
            break;
          case 'right':
            this._xpos+=step;
            break;
          case 'up':
            this._ypos-=step;
            break;
          case 'down':
            this._ypos+=step;
            break;
          default:
            // not move
        }
      }
    });

    // move ruler followed by mouse pointer
    this.addEventListener('mousemove', e => {
      e.preventDefault();
      this._xpos = e.clientX;
      this._ypos = e.clientY;
    });

    // save all important elements for afterward manipulation
    this.tapes = this.shadowRoot.querySelector('section');
    this.layer = this.shadowRoot.querySelector('svg');
    this.xTapes = this.shadowRoot.querySelector('.x-tapes');
    this.yTapes = this.shadowRoot.querySelector('.y-tapes');
    this.xLine = this.shadowRoot.getElementById('x-line');
    this.yLine = this.shadowRoot.getElementById('y-line');
  }

  updated() {
    let yTapesNodes = this.shadowRoot.querySelectorAll('.y-tapes');
    let xTapesNodes = this.shadowRoot.querySelectorAll('.x-tapes');
    
    if (yTapesNodes.length === 0 && xTapesNodes.length === 0) return;

    // convert node list to Array
    let yTapes = Array.prototype.slice.call(yTapesNodes, 0);
    let xTapes = Array.prototype.slice.call(xTapesNodes, 0);

    // sort two list of tapes
    yTapes.sort((a, b) => {
      return a.firstElementChild.innerText - b.firstElementChild.innerText;
    });
    xTapes.sort((a, b) => {
      return a.firstElementChild.innerText - b.firstElementChild.innerText;
    });

    // update value of tapes
    yTapes.forEach((el, index) => {
      if (index && Number(el.firstElementChild.innerText) > Number(yTapes[index-1].firstElementChild.innerText)) {
        el.lastElementChild.innerHTML = '&nbsp;+' + (el.firstElementChild.innerText - yTapes[index-1].firstElementChild.innerText) + '&nbsp;';
      } else {
        el.lastElementChild.innerHTML = '';
      }
    });
    xTapes.forEach((el, index) => {
      if (index && Number(el.firstElementChild.innerText) > Number(xTapes[index-1].firstElementChild.innerText)) {
        el.lastElementChild.innerHTML = '&nbsp;+' + (el.firstElementChild.innerText - xTapes[index-1].firstElementChild.innerText) + '&nbsp;';
      } else {
        el.lastElementChild.innerHTML = '';
      }
    });
  }

  render() {
    return html`
      <svg xmlns="http://www.w3.org/2000/svg" version="1.1">
        ${this.lines.map(newLine => newLine.line)}
        <line id="x-line" x1="0%" y1=${this._ypos} x2="100%" y2=${this._ypos} style="stroke-width:1px;stroke:red;stroke-opacity:1;" />
        <line id="y-line" x1=${this._xpos}  y1="0" x2=${this._xpos}  y2="100%" style="stroke-width:1px;stroke:red;stroke-opacity:1;" />
      </svg>

        <section style="float:left">
        ${this.lines.map(newLine => newLine.tapes)}
        <span class="y-tapes">
          <span class="tapes x-pos"
            style="top:0; left:${this._xpos + 'px'}">&nbsp;${this._xpos}&nbsp;</span>
          <span class="tapes x-distance"
            style="left:${this._xpos + 'px'}"></span>
        </span>
        <span class="x-tapes">
          <span class="tapes y-pos"
            style="left:0; top:${this._ypos + 'px'}">&nbsp;${this._ypos}&nbsp;</span>
          <span class="tapes y-distance"
            style="top:${this._ypos + 'px'}"></span>
        </span>
      </section>
    `;
  }
}

customElements.define('ruler-layer', RulerLayer);
