import { LitElement, html, css} from 'lit-element';
import './globals/index';
import './components/SettingPanel';
import './components/MainBoard';
import './components/GridLayer';
import './components/RulerLayer';

class PageBody extends LitElement {
  static get styles() {
    return [
      GlobalStyles,
      css`
        :host {
          --scale: 1;
          display: block;
          width: 100%;
          height: 100%;
          transform-origin: top left;
          transform: scale(var(--scale));
        }
      `
    ];
  } // style()

  static get properties() {
    return {
      _scale: { type: Number },
      _viewportWidth: { type: Number },
      _viewportHeight: { type: Number }
    }
  } // properties()

  constructor() {
    super();

    this._scale = 1;
    this._viewportWidth = Math.floor(window.visualViewport.width);
    this._viewportHeight = Math.floor(window.visualViewport.height);

    // state properties
    this.state = {
      activeLayer: Global.activeLayer,
      setting: {
        url: Global.setting.url,
        viewportW: Global.setting.viewportW,
        viewportH: Global.setting.viewportH,
        updatedAt: 0
      },
      stuff: [].concat(Global.stuff)
    }

  } // constructor()

  firstUpdated() {
    this.addEventListener('stateChanged', e => {
      // layer gains control
      if (this.shadowRoot.activeElement.nodeName != this.state.activeLayer.toUpperCase()) {
        this.shadowRoot.querySelector(this.state.activeLayer).focus();
      }
    });

    // update viewport of board on window resize
    window.addEventListener('resize', e => {
      let scale = Number(getComputedStyle(this).getPropertyValue('--scale').trim());
      if (scale < 1) {
        this.scaledResize();
      }
    });

    window.addEventListener('keydown', e => {
      if (e.ctrlKey && e.keyCode === 221) {  // ctrl + ]
        this._scale = Number(getComputedStyle(this).getPropertyValue('--scale').trim()) + 0.05;
        this.style.setProperty('--scale', this._scale);
        if (this._scale < 1) {
          this.scaledResize();
        } else if (this._scale === 1) {
          this.style.setProperty('width', '');
          this.style.setProperty('height', '');
          this._viewportWidth = window.visualViewport.width;
          this._viewportHeight = window.visualViewport.height;
        } else {
          this._scale = Number(getComputedStyle(this).getPropertyValue('--scale').trim());
        }
      } else
      if (e.ctrlKey && e.keyCode === 219) {  // ctrl + [
        this._scale = Number(getComputedStyle(this).getPropertyValue('--scale').trim()) - 0.05;
        this.style.setProperty('--scale', this._scale);
        if (this._scale < 1) {
          this.scaledResize();
        }
      }
    });

    // initial set active layer
    this.shadowRoot.querySelector(this.state.activeLayer).focus();
  }

  scaledResize() {
    let scaledSize = this.getBoundingClientRect();
    let newWidth = Math.ceil((1+(window.visualViewport.width - scaledSize.width) / scaledSize.width) * this.offsetWidth);
    let newHeight = Math.ceil((1+(window.visualViewport.height - scaledSize.height) / scaledSize.height) * this.offsetHeight);
    this.style.setProperty('width', newWidth + 'px');
    this.style.setProperty('height', newHeight + 'px');
    this._viewportWidth = newWidth;
    this._viewportHeight = newHeight;
  }

  render() {
    return html`
      <setting-panel></setting-panel>
      <main-board tabindex="-1"
        _viewportWidth=${this._viewportWidth}
        _viewportHeight=${this._viewportHeight}
        _scale=${this._scale}></main-board>
      <grid-layer></grid-layer>
      <ruler-layer tabindex="1"></ruler-layer>
    `;
  }
}

customElements.define('page-body', PageBody);
